import React, { Component } from 'react';
import TodoListTemplate from './components/TodoListTemplate';
import Form from './components/Form';
import TodoItemList from './components/TodoItemList';

class App extends Component {
  id = 3;
  state = {
    input: '',
    todos: [
      { id: 0, text: '컴포넌트 구성하기', checked: true},
      { id: 1, text: '상태관리 하기', checked: true},
      { id: 2, text: '컴포넌트 최적화', checked: false},
    ]
  }
  handleChange = (e) => { //텍스트 내용 바뀌면 state 업데이트
    this.setState({
      input: e.target.value
    });
  }
  handleCreate = () => { //버튼이 클릭되면 새로운 todo 생성 후 todos 업데이트
    const { input, todos } = this.state;
    if(input !== '') { //''는 항목에 추가되지 않도록
      this.setState({
        input: '',
        todos: todos.concat({
          id: this.id++,
          text: input,
          checked: false
        })
      });
    }
  }
  handleEnter = (e) => {
    if(e.key === 'Enter') {
      this.handleCreate();
    }
  }
  handleToggle = (id) => {
    const { todos } = this.state;
    const index = todos.findIndex(todo => todo.id === id);
    const selected = todos[index]; //선택한 객체
    const nextTodos = [...todos];
    nextTodos[index] = { 
      ...selected, 
      checked: !selected.checked //이렇게 똑똑할수가...!
    };

    this.setState({
      todos: nextTodos
    });
  }
  handleRemove = (id) => {
    const { todos } = this.state;
    this.setState({
      todos: todos.filter(todo => todo.id !== id)
    });
  }

  render() {
    const { input, todos } = this.state;
    const { 
      handleChange,
      handleCreate,
      handleEnter,
      handleToggle,
      handleRemove
    } = this;

    return (
      <TodoListTemplate form={(
        <Form
          value={input}
          onKeyPress={handleEnter}
          onChange={handleChange}
          onCreate={handleCreate}
        />
      )}>
        <TodoItemList todos={todos} onToggle={handleToggle} onRemove={handleRemove}/>
      </TodoListTemplate>
    );
  }
}

export default App;
